	module.exports = {"data":{"markdownRemark":{"html":"<p>The administrator of your security team can customize views in your inbox. The administrator can:</p>\n<ul>\n<li>Add an inbox view</li>\n<li>Update existing views</li>\n<li>Change the order of views</li>\n<li>Remove views</li>\n<li>Temporarily show and hide views</li>\n</ul>\n<h3>Adding and Updating Views</h3>\n<p>To add a new View:</p>\n<ol>\n<li>Go to your security team's inbox.</li>\n<li>Click <strong>Show filters</strong>.</li>\n<li>Select the filters you'd like to see in your new inbox view. A <strong>Custom</strong> view will show in your inbox bar with a <strong>Save</strong> button next to it.</li>\n<li>Click the <strong>Save</strong> button.</li>\n<li>The <strong>Save view</strong> window will appear where you can select to create a new view or overwrite an existing one in the <strong>Action</strong> field. </li>\n<li>Type the View name in the <strong>View name</strong> field.</li>\n<li>Click <strong>Save view</strong>. </li>\n</ol>\n<p>The changes will be made directly in your inbox. </p>\n<h3>Changing the Order of Views</h3>\n<p>To change the order of views:</p>\n<ol>\n<li>Go to your program's <strong>Settings > Program > Inbox Views</strong>. </li>\n<li>Click and drag the icon next to the View name to the order you want the View to appear.  </li>\n</ol>\n<h3>Removing Views</h3>\n<p>To permanently remove a View from your inbox:</p>\n<ol>\n<li>In <strong>Settings > Program > Inbox Views</strong>, select <strong>Remove</strong>. A window will pop up confirming whether you want to remove the view. </li>\n<li>Click <strong>Ok</strong>. </li>\n</ol>\n<h3>Showing and Hiding Views</h3>\n<p>To show and hide Views: </p>\n<ol>\n<li>In <strong>Settings > Program > Inbox Views</strong>, select <strong>Shown</strong> next to the View you want to hide. The View will then be hidden. <em>Note: Shown means that the inbox view currently appears in your Inbox.</em> </li>\n<li>When a View is hidden, select <strong>Hidden</strong> to show the View.</li>\n</ol>\n<p><em>Note: There is a *Preview</em> section at the bottom of the page that enables you to see a preview of what your Views will look like in the inbox.* </p>\n<p><img src=\"https://github.com/Hacker0x01/docs.hackerone.com/blob/master/docs/programs/images/inbox-views.png?raw=true\" alt=\"inbox-view\"></p>","frontmatter":{"path":"/programs/inbox-views","title":"Inbox Views"}}},"pathContext":{}}

